import React from 'react';

import './Settings.css';

class Settings extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            mapSize:50,
            brush: "grass"
        }

        this.handleChangeMapSize = this.handleChangeMapSize.bind(this)
        this.handleChangeBrush = this.handleChangeBrush.bind(this)
    }

    handleChangeMapSize(event) {
        this.setState({mapSize: event.target.value})
        if(this.props.changeCallback !== null) {
            var state = this.state
            state.mapSize = event.target.value
            console.log(state.mapSize)
            this.props.changeCallback(state)
        }
    }

    handleChangeBrush(event) {
        this.setState({brush: event.target.value})
        if(this.props.changeCallback !== null) {
            var state = this.state
            state.brush = event.target.value
            this.props.changeCallback(state)
        } 
    }

    render() {
        return (
            <div className='settings_wrapper'>
                <label>
                Map size:
                <input type="number" value={this.state.mapSize} onChange={this.handleChangeMapSize} />
                </label><br/>
            </div>
        );
    }
}

export default Settings;


