import React from 'react';

import './Editor.css';
import Settings from './Settings/Settings.react'
import Map from './Map/Map.react'
import { Button, ButtonGroup, ButtonToolbar } from 'reactstrap'
import ReactDOM from 'react-dom';
import fontawesome from '@fortawesome/fontawesome'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
class Editor extends React.Component {
    constructor(props) {
        super(props);
        this.state = {settings:{mapSize:50, brush:"none"}, selectedTiles:null}
        this.onClickTile = this.onClickTile.bind(this)
        this.onMapSetup = this.onMapSetup.bind(this)
        this.changeTileTypes = this.changeTileTypes.bind(this)
        this.clearSelection = this.clearSelection.bind(this)
        this.handleBrushChange = this.handleBrushChange.bind(this)
    }

    handleChangeSettings(settingsArr) {
        this.setState({settings:settingsArr})
    }

    handleBrushChange(brush) {
        var settings = this.state.settings
        settings.brush = brush
        this.setState({settings: settings})
    }


    changeTileTypes(e) {
        if(this.state.selectedTiles && this.state.map) {
            var map = this.state.map
            for (var tile in this.state.selectedTiles) {
                tile = this.state.selectedTiles[tile]
                if(map && map[tile.x] && map[tile.x][tile.y]) {
                    map[tile.x][tile.y].type = e.target.value
                    console.log("Changing tile to:" + e.target.value)
                }
            }
            this.setState({map:map})
        }
    }

    onClickTile(tile) {
        //this.setState({selectedTile: tile})
        var selectedTiles = this.state.selectedTiles ? this.state.selectedTiles : {}
        if(!selectedTiles[String(tile.x)+"-"+String(tile.y)] && this.state.settings.brush === "none") {
            selectedTiles[String(tile.x)+"-"+String(tile.y)] = tile
        } else if(this.state.settings.brush === "none") {
            //console.log("Should delete")
            delete selectedTiles[String(tile.x)+"-"+String(tile.y)]
        }
        //this.setState({selectedTiles:selectedTiles})
        
        var map = this.state.map
        if(map && map[tile.x] && map[tile.x][tile.y]) {
            if(map[tile.x][tile.y].selected === 'true') {
                delete map[tile.x][tile.y].selected
            } else {
                if(this.state.settings.brush !== "none") {
                    map[tile.x][tile.y].type = this.state.settings.brush
                } else {
                    map[tile.x][tile.y].selected = 'true'
                }
            }
            //this.setState({map:map})
           // console.log(this.state.map[tile.x][tile.y])
        }
        this.setState({selectedTiles:selectedTiles, map:map})
    }

    onMapSetup(map) {
        this.setState({map:map})
    }

    clearSelection() {
        if(this.state.selectedTiles && this.state.map) {
            var map = this.state.map
            for (var tile in this.state.selectedTiles) {
                tile = this.state.selectedTiles[tile]
                if(map && map[tile.x] && map[tile.x][tile.y]) {
                    delete map[tile.x][tile.y].selected
                }
            }
            this.setState({selectedTiles:{},map:map})
        }
    }

    renderSelectedTile() {
        //console.log(this.state.selectedTiles == [])

        if(this.state.selectedTiles !== null && Object.keys(this.state.selectedTiles).length > 0) {
            //console.log(JSON.stringify(this.state.selectedTiles))
            //console.log(this.state.selectedTiles)
            return (
                <div className="selectedTile">Selected tiles: {Object.keys(this.state.selectedTiles).length}<br/>
                Change tiletype of selected tiles to:<br/>
                    <button className="button pickWater" type="button" value="water" onClick={this.changeTileTypes}>Water</button><br/>
                    <button className="button pickRock" type="button" value="rock" onClick={this.changeTileTypes}>Rock</button><br/>

                    <button className="button pickGrass" type="button" value="grass" onClick={this.changeTileTypes}>Grass</button><br/>
                    <button className="button pickTree" type="button" value="tree" onClick={this.changeTileTypes}>Tree</button><br/>
                </div>
            )
        }
    }
//            <div className="left-sidebar">
//Settings: <Settings changeCallback={this.handleChangeSettings.bind(this)} /><br/>
//</div>
    render() {
        var settings = this.state.settings
        return (
            <div className='fulleditor_wrapper'>
                <div className="top-toolbar">
                    <ButtonToolbar>
                        <ButtonGroup>
                            <Button className={this.state.settings.brush === "none" ? "btn-selected" : ""} value="none" onClick={(e) => {this.handleBrushChange("none")}}><i className="fa fa-mouse-pointer" style={{"font-size": 14}}></i></Button>
                            <Button className={this.state.settings.brush === "grass" ? "btn-selected" : ""} value="grass" onClick={(e) => {this.handleBrushChange("grass")}}><i className="fa fa-pagelines" style={{"font-size": 14}}></i></Button>
                            <Button className={this.state.settings.brush === "tree" ? "btn-selected" : ""} value="tree" onClick={(e) => {this.handleBrushChange("tree")}}><i className="fa fa-tree" style={{"font-size": 14}}></i></Button>
                            <Button className={this.state.settings.brush === "water" ? "btn-selected" : ""} value="water" onClick={(e) => {this.handleBrushChange("water")}}><i className="fa fa-tint" style={{"font-size": 14}}></i></Button>
                            <Button className={this.state.settings.brush === "rock" ? "btn-selected" : ""} value="rock" onClick={(e) => {this.handleBrushChange("rock")}}><i className="fa fa-cloud" style={{"font-size": 14}}></i></Button>
                        </ButtonGroup>
                        <ButtonGroup>
                            <Button onClick={this.clearSelection}><i className="fa fa-times-circle" style={{"font-size": 14}}></i></Button>
                        </ButtonGroup>
                    </ButtonToolbar>
                </div>
            <div className='editor_wrapper'>

            <Map Size={settings.mapSize} onClickTile={this.onClickTile} onMapSetup={this.onMapSetup} MapArray={this.state.map} /><br/>
            {this.renderSelectedTile()}
           
            </div>
            </div>
        );
    }
}

export default Editor;


