import React from 'react';
import ReactDOM from 'react-dom'
import './Map.css';
import Tile from './Tile/Tile.react'
import DragScroll from 'react-dragscroll'
import Grass from './tiles/grass.png'
import Water from './tiles/water.png'
import Tree from './tiles/trees.png'
import Rock from './tiles/mountain.png'
import SelectedFrame from './tiles/selected.png'

class Map extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tileSizeW: props.TileWidth ? props.TileWidth : 64,
            tileSizeH: props.TileHeight ? props.TileHeight : 64,
            mapSize: props.Size ? props.Size : 50,
            mapArr: props.MapArray ? props.MapArray : null
        }
        this.onClickTile = this.onClickTile.bind(this)
        //this.editorRef = ReactDOM.createRef()
    }

    componentDidMount() {
        this.buildMapArray()
    }

    componentWillReceiveProps(next) {
        //this.setState({mapSize: next.Size})
        if(next.MapArray) {
            //console.log("Recieve map array")
            this.setState({mapArr:next.MapArray})
        }
    }

    editorRef(el) {
        el = el.refs.container
        var el2 = el.firstChild
        console.log(el.firstChild)
        var centerX = (el2.offsetWidth / 2) - el.offsetWidth / 2
        var centerY = (el2.offsetHeight / 2) - el.offsetHeight / 2
        el.scrollLeft = centerX
        el.scrollTop = centerY
    }

    updateMapArray() {
        this.buildMapArray() //Todo: Add resizing of map on props change
    }

    buildMapArray() {
        var mapArr = []
        for(var y=0; y < this.state.mapSize;y++) {
            var xArr = []
            for(var x=0; x < this.state.mapSize;x++) {
                xArr.push({x:y,y:x, type:"grass"})
            }
            mapArr.push(xArr)
        }
        this.setState({mapArr:mapArr})
        this.props.onMapSetup(mapArr)
    }

    onClickTile(tile, e) {
        this.props.onClickTile(tile)
    }
    renderJsonMap() {
        if(this.state.mapArr) {
            return (
                <textarea className="jsonmap" rows={20} value={JSON.stringify(this.state.mapArr)} readOnly={true} />
            )
        }
    }

    getBgImage(type) {
        if(type === "grass") {
            return Grass
        } else if(type === "rock") {
            return Rock
        } else if(type === "water") {
            return Water
        } else if(type === "tree") {
            return Tree
        }
    }

    renderMap() {
        if(!this.state.mapArr) {
            return;
        }
        return <DragScroll className="map-container dragscroll" id="map" ref={this.editorRef}><div className="map-inner" style={{height:this.state.tileSizeH*this.state.mapSize, width:this.state.tileSizeW*this.state.mapSize}}> {this.state.mapArr.map((row,index) => {
            return (<div className="rowz">
            {row.map(tile => { return(
                <div className={"tile " + (tile.selected ? "selected " : "") + (tile.type)} onClick={(e) => this.onClickTile(tile,e)} data-x={tile.x} data-y={tile.y} style={{width:this.state.tileSizeW, height:this.state.tileSizeH, backgroundImage:"url("+this.getBgImage(tile.type)+")"}}>
                    <div className="text"><span className="hidden">{tile.x},{tile.y}</span></div>
                </div>)
            })}
            </div>)
        })}
         </div>
        </DragScroll>
    }

    render() {
        return (
            <div className='map_wrapper'>
                {this.renderMap()}
                {this.renderJsonMap()}
            </div>
        );
    }
}

export default Map;


