import React, { Component } from 'react';
import Tile from './Tile'
class Map extends Component {
  constructor() {
    super();
    this.state = {
        map:false
    }
  }

  getMapUrl(x=0,y=0,size=10) {
    return "http://localhost:3001/getMap?x="+x+"&y="+y+"&size="+size
  }

  render() {
      let tiles = []
      if(this.state.map) {
        tiles = this.state.map.tiles
      }
      console.log(this.state.map)
    return (
    <div className="grid">
        {tiles.map((row,index) => {
            return (<ol className={index % 2 === 0 ? "odd" : "even"}>
            {row.map(tile => { return(
                <li className={"hex " + (tile.tiletype !== "" ? tile.tiletype : "land")} data-x={tile.x} data-y={tile.y}>
                    <div className="text"><span className="hidden">{tile.x},{tile.y}</span></div>
                </li>)
            })}
            </ol>)
        })}
    </div>
  );
  }
  componentWillReceiveProps() {
    fetch(this.getMapUrl())
    .then(response => response.json())
    .then(data => this.setState({map:data}))
  }
  componentDidMount() {
    fetch(this.getMapUrl())
    .then(response => response.json())
    .then(data => this.setState({map:data}))
  }
}

export default Map;



